const app = getApp()
export default {
	data() {
		return {
			list: []
		}
	},
	onLoad() {
		this.getList()
	},
	methods: {
		getList(){
			uni.showLoading({
				title: "加载中..."
			});
			uni.request({
				url: `${this.$helper.requestUrl}user/myAgentList`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading()
					res = this.$helper.null2str(res.data);
					console.log(res);
					if (res.status_code == 'ok') {
						this.list = res.data
					} else {
						this.list = []
					}
				}
			});
		}
	}
}
